.foto__image {
  display: block;
  max-width: 282px;
  max-height: 282px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}





.foto__image:hover {
  transform: scale(1.025, 1.025);
  transition: 0.3s ease;
}
