.logo {
border-bottom: 1px solid rgba(84, 84, 84, .7);
max-width: 880px;
width: 100%;
margin: 0 auto;
padding-top: 45px;
padding-bottom: 41px;
flex: 0 0 auto;
display: flex;
justify-content: space-between;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
.logo {
padding-top: 28px;
padding-bottom: 31px;
}
}
